*{margin:0;padding:0;box-sizing:border-box}body{background-color:#38654e63}#timer{width:100%;height:100%;display:flex;justify-content:center;margin-top:50px}#timer #timer_div{background-color:#888888;display:flex;flex-direction:column;width:400px;text-align:center;border:1px solid #000;border-radius:40%;box-shadow:inset 5px 5px 10px #000000, inset -5px -5px 10px #000000}#timer #timer_div h1{margin-top:50px;margin-bottom:30px;font-size:50px}#timer #timer_div #changes{display:flex;justify-content:center}#timer #timer_div #changes .break_changes{margin-right:25px;display:flex;flex-direction:column}#timer #timer_div #changes .session_changes{display:flex;flex-direction:column}#timer #timer_div #changes .arrow_icons_div{display:flex;justify-content:center;font-size:20px}#timer #timer_div #changes .arrow_icons_div img,#timer #timer_div #changes .arrow_icons_div i{width:50px;height:30px;font-size:30px}#timer #timer_div #changes .arrow_icons_div img:active,#timer #timer_div #changes .arrow_icons_div i:active{background:rgba(157,157,157,0.592157);-webkit-box-shadow:inset 0px 0px 5px rgba(157,157,157,0.592157);-moz-box-shadow:inset 0px 0px 5px rgba(157,157,157,0.592157);box-shadow:inset 0px 0px 5px rgba(157,157,157,0.592157);outline:none}#timer #timer_div #changes .arrow_icons_div div{margin:0 5px}#timer #timer_div #display{width:40%;height:70px;align-self:center;margin:30px 0;border:1px solid #000;box-shadow:inset 5px 5px 10px #000000, inset -5px -5px 10px #000000;background-color:#38654ebb}#timer #timer_div #display #time-left{font-size:30px;font-weight:700}#timer #timer_div #actions_div{display:flex;justify-content:center;margin-bottom:30px}#timer #timer_div #actions_div img,#timer #timer_div #actions_div i{width:50px;height:50px;font-size:45px}#timer #timer_div #actions_div img:active,#timer #timer_div #actions_div i:active{background:rgba(157,157,157,0.592157);-webkit-box-shadow:inset 0px 0px 5px rgba(157,157,157,0.592157);-moz-box-shadow:inset 0px 0px 5px rgba(157,157,157,0.592157);box-shadow:inset 0px 0px 5px rgba(157,157,157,0.592157);outline:none}#timer #timer_div #actions_div #reset:hover{color:#ec1c1c}
